import React from 'react';
import { connect } from "react-redux";
import { Helmet } from "react-helmet";
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr'
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import BusinessTypesTable from "./Tables/BusinessTypesTable";
import BusinessTypesModal from "./Modals/BusinessTypesModal";


class BusinessTypes extends React.Component {
    constructor(props, context) {
        super(props, context);
        var Dropdownvalidation = 0;
        this.state = {
            errors: {},
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            BUSINESS_TYPE_ID: 0,
            BUSINESS_TYPE_NAME: '',
            SYS_STATUS_ID: 0,
            CREATEDBY: '',
            MODIFIEDBY: '',
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            searchcolumns: [],
            systemstatussearchdata: [],
            systemstatussearchFilteredData: [],
            systemstatussearchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateStatus();

    }

    handleValidation(){
        let errors = {};
        let formIsValid = true;

        if(!this.state.BUSINESS_TYPE_NAME){
            formIsValid = false;
            errors["BusinessTypeName"] = "Please Enter Business Type Name";
        }

        if(!this.state.SYSTEMSTATUSTYPEDESCRIPTION){
            formIsValid = false;
            errors["SystemStatus"] = "Please select System Status Type";
        }

        if(!this.state.SYS_STATUS_ID)
        {
         formIsValid = false;
         errors["SystemStatus"] = "Please select System Status Type";
        }

        if(this.Dropdownvalidation === 1)
        {
            errors["SystemStatus"] = "";
            this.Dropdownvalidation = 0;
        }

        this.setState({errors: errors});
        return formIsValid;
    }

    populateData() {

        const getdata = `
            query{
                BusinessTypes{
                    BUSINESS_TYPE_ID
                    BUSINESS_TYPE_NAME
                    SYS_STATUS_ID
                    SYSTEMSTATUS{
                        SYS_STATUS_ID
                        SYS_STATUS_NAME
                    }
                    CREATEDBY
                    MODIFIEDBY
                    CREATED_DATE
                    MODIFIED_DATE
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.BusinessTypes
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"SET"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                systemstatussearchdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        debugger;

        if(this.handleValidation()){

            const form = `
            mutation{
                addBusinessType(
                    BUSINESS_TYPE_NAME:"${this.state.BUSINESS_TYPE_NAME}",
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        BUSINESS_TYPE_ID
                }
            }
            `;
            console.log(form);
            const formEdit = `
            mutation{
                updateBusinessType(
                    BUSINESS_TYPE_ID:${this.state.BUSINESS_TYPE_ID},
                    BUSINESS_TYPE_NAME:"${this.state.BUSINESS_TYPE_NAME}",
                    SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                    CREATEDBY:"${this.state.CREATEDBY}",
                    MODIFIEDBY:"${this.state.MODIFIEDBY}",
                    CREATED_DATE:"${this.state.CREATED_DATE}",
                    MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                    ){
                        BUSINESS_TYPE_ID
                }
            }
            `;

            if (this.state.InsertUpdate === 'Insert') {
                API.create(form)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Load Data');
                        }
                    })
            }
        }
    }

    handleDelete = (event) => {
        event.preventDefault();
        debugger;
        const formDelete = `
        mutation{
            deleteBusinessType(
                BUSINESS_TYPE_ID:${this.state.BUSINESS_TYPE_ID}
                ){
                    BUSINESS_TYPE_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            opensearch: false,
        });

        this.Dropdownvalidation=1;
        this.handleValidation();
    };
    handleEditData = (e) => {
        this.setState({
            BUSINESS_TYPE_ID: e.BUSINESS_TYPE_ID,
            BUSINESS_TYPE_NAME: e.BUSINESS_TYPE_NAME,
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYSTEMSTATUS.SYS_STATUS_NAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            BUSINESS_TYPE_ID: e.BUSINESS_TYPE_ID,
            BUSINESS_TYPE_NAME: e.BUSINESS_TYPE_NAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            BUSINESS_TYPE_NAME: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: ''
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.BUSINESS_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_TYPE.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.BUSINESS_TYPE_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.systemstatussearchdata
        let filteredData = this.state.systemstatussearchFilteredData
        this.setState({ systemstatussearchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    changeEventandValidation = (e) => {
      
        if(e.target.name==="BusinessTypeName")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            BUSINESS_TYPE_NAME:e.target.value
            });
        }
        if(e.target.name==="SystemStatus")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYSTEMSTATUSTYPEDESCRIPTION:e.target.value
            });
        }


        if(e.target.name==="SystemStatusID")
        {
            this.setState({ 
            [e.target.name]: e.target.value ,
            SYS_STATUS_ID:e.target.value
            });
        }        
         
    this.handleValidation();
   
};

    render() {
        const { data, value, filteredData, systemstatussearchdata, systemstatussearchvalue } = this.state
        return (
            <>
                <Helmet><title>Business Types</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Business Types"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Business Types</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={systemstatussearchvalue}
                                        data={systemstatussearchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <BusinessTypesTable
                                        role={this.constructor.name}
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <BusinessTypesModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}

                                        SystemStatusTypeID={this.state.SYS_STATUS_ID}
                                        handleChangeSystemStatusTypeID={e => this.setState({ SYS_STATUS_ID: e.target.value })}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}
                                        handleChangeSystemStatusTypeDescription={e => this.setState({ SYSTEMSTATUSTYPEDESCRIPTION: e.target.value })}

                                        BusinessTypeName={this.state.BUSINESS_TYPE_NAME}
                                        changeBusinessTypeName={e => this.setState({ BUSINESS_TYPE_NAME: e.target.value })}

                                        changeEventandValidation={this.changeEventandValidation}
                                        BusinessTypeNameError={this.state.errors["BusinessTypeName"]}
                                        SystemStatusTypeError={this.state.errors["SystemStatus"]}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(BusinessTypes));